import React from 'react';
import {connect} from "react-redux";
import logo from "../../assets/logo.png";
import {Link} from 'react-router-dom';


class Profile extends React.Component {
    state = {
        user : {}}
    render() {
        return(
            <div>
                {
                this.props.users.map(user =>
                    <div className="container">
                        <div className="col-md-8 offset-md-2 mt-5">
                            <div className="text-center mb-3">
                                <img src={logo} className="form-logo"/>
                            </div>
                            <h1 className="text-center">Profile</h1>
                            <div className="content-wrapper">
                                <div className="form-group row wbdv-field wbdv-username">
                                    <label htmlFor="username"
                                           className="col-sm-3 col-form-label">
                                        Username
                                    </label>
                                    <div className="col-sm-9">
                                        {user.userName}
                                    </div>
                                </div>

                                <div className="form-group row wbdv-field wbdv-phone">
                                    <label htmlFor="phone"
                                           className="col-sm-3 col-form-label">
                                        PhoneNumber
                                    </label>
                                    <div className="col-sm-9">
                                        {user.Phone}
                                    </div>
                                </div>

                                <div className="form-group row wbdv-field wbdv-email">
                                    <label htmlFor="email"
                                           className="col-sm-3 col-form-label">
                                        Email
                                    </label>
                                    <div className="col-sm-9">
                                        {user.Email}
                                    </div>
                                </div>
                                <div className="form-group row wbdv-field wbdv-address">
                                    <label htmlFor="address"
                                           className="col-sm-3 col-form-label">
                                        Address
                                    </label>
                                    <div className="col-sm-9">
                                        {user.Address}
                                    </div>
                                </div>

                                <div className="form-group row wbdv-field wbdv-userLevel">
                                    <label htmlFor="userLevel"
                                           className="col-sm-3 col-form-label">
                                        userLevel
                                    </label>
                                    <div className="col-sm-9">
                                        {user.userLevel}
                                    </div>
                                </div>

                                <div className="form-group row wbdv-field wbdv-reviews">
                                    <label htmlFor="reviews"
                                           className="col-sm-3 col-form-label">
                                        Reviews
                                    </label>
                                    <div className="col-sm-9">
                                        {user.Reviews.map(review =>
                                            <div>{review}</div>
                                        )}
                                    </div>
                                </div>

                                <div className="form-group row wbdv-field wbdv-orders">
                                    <label htmlFor="orders"
                                           className="col-sm-3 col-form-label">
                                        Orders
                                    </label>
                                    <div className="col-sm-9">
                                        {user.Orders.map(order =>
                                            <div>{order}</div>
                                        )}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>)
                }
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return{
        users: state.user.users
    }
}
export default connect(mapStateToProps)(Profile);

