import React from 'react';
import logo from '../../assets/logo.png';
import {Link} from "react-router-dom";
import $ from 'jquery'

class SignIn extends React.Component {

  state = {
    username: '',
    password: ''
  }

  submitLogin = (e) => {
    const username = document.getElementById('username').value
    const password = document.getElementById('password').value
    const data = {
        username: username,
        password: password
    };
    console.log(data);
    $.ajax({
        url: '/login.json',
        data: data,
        type: 'POST',
        success: (res) => {
            console.log(res)
        },
        error: (err) => {
            console.log(err)
        }
    });
  }

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    })
  };

  render() {
    return (
        <div className="container">
          <div className="col-md-8 offset-md-2 mt-5">
            <div className="text-center mb-3">
              <img src={logo} className="form-logo"/>
            </div>

            <h1 className='text-center'>Sign In</h1>

            <div className="content-wrapper">
              <div className="form-group row wbdv-field wbdv-username">
                <label htmlFor="username"
                       className="col-sm-2 col-form-label">
                  Username
                </label>
                <div className="col-sm-10">
                  <input className="form-control"
                         id="username"
                         placeholder="Alice"
                         onChange={this.handleChange}/>
                </div>
              </div>
              <div className="form-group row wbdv-field wbdv-password">
                <label htmlFor="password"
                       className="col-sm-2 col-form-label">
                  Password
                </label>
                <div className="col-sm-10">
                  <input type="password"
                         className="form-control"
                         id="password"
                         placeholder="123qwe#$%"
                         onChange={this.handleChange}/>
                </div>
              </div>
              <div className="form-group row">
                <label className="col-sm-2 col-form-label"></label>
                <div className="col-sm-10">

                  <div className="row mb-3">
                    <div className="col-sm-9">
                      <a className="btn btn-primary btn-block wbdv-button wbdv-login mb-3"
                         onClick={this.submitLogin}
                         role="button">
                        Sign in
                      </a>
                    </div>

                    <div className="col-sm-3">
                      <Link to="/"
                            className="btn btn-danger btn-block"
                            role="button">
                        Cancel
                      </Link>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-6 wbdv-link wbdv-forgot-password">
                      <a href="#" role="button">Forgot Password?</a>
                    </div>

                    <div className="col-6 wbdv-link wbdv-register">
                      <Link className="float-right"
                            to="/signup"
                            role="button">
                        Sign up
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>
    )
  }
}

export default SignIn;
