import React from 'react';
import logo from "../../assets/logo.png";
import {Link} from 'react-router-dom';

class SignUp extends React.Component {

  state = {
    username: '',
    password: '',
    email: '',
    firstName: '',
    lastName: '',
    verifyPassword: ''
  }

  submitChanges = (e) => {
    console.log(e);
  }

  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    })
  };

  render() {
    return (
        <div className="container login-wrapper">
          <div className="col-md-8 offset-md-2 mt-5">
            <div className="text-center mb-3">
              <img src={logo} className="form-logo"/>
            </div>
            <h1 className="text-center">Sign Up</h1>

            <div className="content-wrapper">
              <div className="form-group row wbdv-field wbdv-username">
                <label htmlFor="username"
                       className="col-sm-3 col-form-label">
                  Username
                </label>
                <div className="col-sm-9">
                  <input className="form-control"
                         id="username"
                         placeholder="Alice"
                         onChange={this.handleChange}/>
                </div>
              </div>
              <div className="form-group row wbdv-field wbdv-password">
                <label htmlFor="password"
                       className="col-sm-3 col-form-label">
                  Password
                </label>
                <div className="col-sm-9">
                  <input type="password"
                         className="form-control"
                         id="password"
                         placeholder="123qwe#$%"
                         onChange={this.handleChange}/>
                </div>
              </div>

              <div className="form-group row wbdv-field wbdv-password-verify">
                <label htmlFor="verifyPassword"
                       className="col-sm-3 col-form-label">
                  Verify Password
                </label>
                <div className="col-sm-9">
                  <input type="password"
                         className="form-control"
                         id="verifyPassword"
                         placeholder="123qwe#$%"
                         onChange={this.handleChange}/>
                </div>
              </div>

              <div className="form-group row wbdv-field">
                <label htmlFor="firstName"
                       className="col-sm-3 col-form-label">
                  First Name
                </label>
                <div className="col-sm-9">
                  <input type="text"
                         className="form-control"
                         id="firstName"
                         placeholder="Alice"
                         onChange={this.handleChange}/>
                </div>
              </div>

              <div className="form-group row wbdv-field">
                <label htmlFor="lastName"
                       className="col-sm-3 col-form-label">
                  Last Name
                </label>
                <div className="col-sm-9">
                  <input type="text"
                         className="form-control"
                         id="lastName"
                         placeholder="Wonderland"
                         onChange={this.handleChange}/>
                </div>
              </div>

              <div className="form-group row wbdv-field ">
                <label htmlFor="email"
                       className="col-sm-3 col-form-label">
                  Email
                </label>
                <div className="col-sm-9">
                  <input type="email"
                         className="form-control"
                         id="email"
                         placeholder="alice@wonderland.com"
                         onChange={this.handleChange}/>
                </div>
              </div>

              <div className="form-group row">
                <label className="col-sm-3 col-form-label"></label>
                <div className="col-sm-9">

                  <div className="row">
                    <div className="col-sm-9">
                      <a className="btn btn-primary btn-block wbdv-button wbdv-register mb-3"
                         onClick={this.submitChanges}
                         role="button">
                        Sign up
                      </a>
                    </div>

                    <div className="col-sm-3">
                      <Link to="/"
                            className="btn btn-danger btn-block"
                            role="button">
                        Cancel
                      </Link>
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-6">
                      <Link className="float-left wbdv-link wbdv-login"
                            to="/login"
                            role="button">
                        Login
                      </Link>
                    </div>

                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    )
  }
}

export default SignUp;
